/* Container for the forms */
.auth-box-container {
  width: 100%;
  height: 80vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f4f4f4;
}

/* The box that contains both forms */
.auth-box {
  width: 350px;
  height: 400px;
  perspective: 1000px;
  position: relative;
}

.auth-form {
  position: absolute;
  width: 90%;
  height: 100%;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 10px;
  background-color: white;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  backface-visibility: hidden;
  transition: transform 0.6s;
}

.auth-box.flipped .sign-up-form {
  transform: rotateY(180deg);
}

.auth-box.flipped .login-form {
  transform: rotateY(0);
}

.sign-up-form {
  transform: rotateY(0);
}

.login-form {
  transform: rotateY(-180deg);
}

/* Form fields styling */
.auth-form input {
  width: 300px;
  padding: 10px;
  margin: 10px 0;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
}

.auth-form button {
  width: 100%;
  padding: 10px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
}

.auth-form button:hover {
  background-color: #0056b3;
}

/* Text below the forms */
.auth-form p {
  margin-top: 15px;
  text-align: center;
}

.flip-button {
  background: none;
  border: none;
  color: #007bff;
  cursor: pointer;
  padding: 0;
}

.flip-button:hover {
  text-decoration: underline;
}
